import firebase from 'firebase/app';
import 'firebase/auth';

interface ClientConfig {
  CLIENT_ID: string;
  API_KEY: string;
  SCOPES: string;
  DISCOVERY_DOCS: string[];
}

type AuthChangedCallback = (isSignedIn: boolean) => void

class GoogleClient {
  static clientConfig: ClientConfig;

  static currentUser: any;

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  static auth2: any;

  static authChangedCallback: AuthChangedCallback;

  static init(clientConfig: ClientConfig) {
    GoogleClient.clientConfig = clientConfig;

    gapi.load('client:auth2', GoogleClient.initClient);
  }

  private static initClient() {
    const {
      API_KEY, CLIENT_ID, SCOPES, DISCOVERY_DOCS,
    } = GoogleClient.clientConfig;

    gapi.client.init({
      apiKey: API_KEY,
      clientId: CLIENT_ID,
      scope: SCOPES,
      discoveryDocs: DISCOVERY_DOCS,
    }).then(() => {
      // Listen for sign-in state changes.
      GoogleClient.auth2 = gapi.auth2.getAuthInstance();
      GoogleClient.auth2.isSignedIn.listen(GoogleClient.updateSigninStatus);

      // init sign in state
      GoogleClient.updateSigninStatus(GoogleClient.auth2.isSignedIn.get());
    }).catch(() => {
      console.error('Google client init failed');
    });
  }

  private static async updateSigninStatus(isSignedIn: boolean) {
    if (isSignedIn) {
      console.log('signed in');

      GoogleClient.currentUser = GoogleClient.auth2.currentUser.get();
      const authResponse = GoogleClient.currentUser.getAuthResponse(true);
      const credential = firebase.auth.GoogleAuthProvider.credential(
        authResponse.id_token,
        authResponse.access_token,
      );
      await firebase.auth().signInWithCredential(credential);
    } else {
      console.log('signed out');
      await firebase.auth().signOut();
    }

    GoogleClient.authChangedCallback(isSignedIn);
  }

  static onAuthStateChanged(callback: AuthChangedCallback) {
    GoogleClient.authChangedCallback = callback;
  }

  static signIn() {
    if (GoogleClient.auth2) {
      GoogleClient.auth2.signIn();
    } else {
      console.error('Google client not ready');
    }
  }

  static signOut() {
    if (GoogleClient.auth2) {
      GoogleClient.auth2.signOut();
    } else {
      console.error('Google client not ready');
    }
  }
}

export default GoogleClient;
