const firebaseConfig = {
  apiKey: 'AIzaSyBWVvkkFXqZUQdoqbQ28Wv1vW2lkjHB2_0',
  authDomain: 'rainform-d5375.firebaseapp.com',
  projectId: 'rainform-d5375',
  storageBucket: 'rainform-d5375.appspot.com',
  messagingSenderId: '244947259',
  appId: '1:244947259:web:5403facdd1c5885c7fff1e',
  measurementId: 'G-FQ8P6Z4HV7',
};

Vue.use(vmodal, { dialog: true });

new Vue({
  router,
  store,
  created() {
    firebase.initializeApp(firebaseConfig);
    firebase.auth().languageCode = 'zh-TW';

    GoogleClient.init(googleClientConfig);
    GoogleClient.onAuthStateChanged(async (isSignedIn) => {
      if (isSignedIn) {
        if (await UserApi.isAdmin()) {
          FormApi.list().then((memberList) => {
            this.$store.commit('updateMemberList', memberList);
          });
          FormApi.listCeremonyAttendees().then((attendeesList) => {
            this.$store.commit('updateAttendeesList', attendeesList);
          });
          EventApi.retrieve().then((eventInfo) => {
            this.$store.commit('updateEventInfo', eventInfo);
          });

          this.$router.push(this.$store.state.homepageRoute);
        } else {
          // you are not admin
          this.$router.push('/no-auth');
        }
      } else {
        this.$router.push('/');
        this.$store.commit('resetState');
      }
    });
  },
  render: (h) => h(App),
}).$mount('#app');
