import Vue from 'vue';
import VueRouter, { RawLocation, Route } from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Login',
    component: () => import('../views/GoogleLogin.vue'),
  },
  {
    path: '/homepage',
    name: 'HomePage',
    component: () => import('../views/HomePage.vue'),
    children: [
      {
        path: 'memberlist',
        name: 'MemberList',
        component: () => import('../views/MemberList.vue'),
      },
      {
        path: 'viewMemberForm',
        name: 'ViewMemberForm',
        component: () => import('../views/ViewMemberForm.vue'),
      },
      {
        path: 'attendeesList',
        name: 'AttendeesList',
        component: () => import('../views/AttendeesList.vue'),
      },
      {
        path: 'viewMemberInfo',
        name: 'ViewMemberInfo',
        component: () => import('@/views/ViewMemberInfo.vue'),
      },
      {
        path: 'overprintpage',
        name: 'OverprintPage',
        component: () => import('../views/OverprintPage.vue'),
      },
      {
        path: 'eventForm',
        name: 'EventForm',
        component: () => import('../views/EventForm.vue'),
      },
      {
        path: 'ceremonyFormsPage',
        name: 'CeremonyFormsPage',
        component: () => import('../views/CeremonyFormsPage.vue'),
        children: [
          {
            path: 'textname',
            name: 'TextName',
            component: () => import('../components/CeremonyForms/TextName.vue'),
          },
          {
            path: 'personalInformation',
            name: '',
            component: () => import('../components/CeremonyForms/PersonalInformation.vue'),
          },
          {
            path: 'longevity',
            name: 'Longevity',
            component: () => import('../components/CeremonyForms/Longevity.vue'),
          },
          {
            path: 'prayFor',
            name: 'PrayFor',
            component: () => import('../components/CeremonyForms/PrayFor.vue'),
          },
          {
            path: 'attendInfo',
            name: 'AttendInfo',
            component: () => import('../components/CeremonyForms/AttendInfo.vue'),
          },
          {
            path: 'checkForm',
            name: 'CheckForm',
            component: () => import('../components/CeremonyForms/CheckForm.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/no-auth',
    name: 'NoAuthorization',
    component: () => import('../views/NoAuthorization.vue'),
  },
  {
    path: '/dev',
    name: 'Dev',
    component: () => import('../views/Dev.vue'),
  },
];

const router = new VueRouter({
  routes,
  linkActiveClass: 'active',
});

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location: RawLocation): Promise<Route> {
  return new Promise((resolve, reject) => {
    originalPush.call(this, location, () => {
      // on complete

      resolve(this.currentRoute);
    }, (error) => {
      // on abort

      // only ignore NavigationDuplicated error
      if (error.name === 'NavigationDuplicated') {
        resolve(this.currentRoute);
      } else {
        reject(error);
      }
    });
  });
};

export default router;
