import Utils from '@/libs/Utils';
import { EventInfo } from '@/models/EventFormModel';
import firebase from 'firebase/app';
import 'firebase/firestore';

export default class EventApi {
  static async save(event: EventInfo): Promise<void> {
    const db = firebase.firestore();

    const eventId = 'rainform';

    await db.collection('events').doc(eventId).set(Utils.toObject(event));
  }

  static async retrieve(): Promise<EventInfo> {
    const db = firebase.firestore();

    const eventId = 'rainform';

    const docSnp = await db.collection('events').doc(eventId).get();
    const event = docSnp.data() as EventInfo;
    return event;
  }
}
