import Utils from '@/libs/Utils';
import { AttendInfo, AttendInfoModel } from '@/models/CeremonyFormModel';
import { FormModel } from '@/models/FormModel';
import firebase from 'firebase/app';
import 'firebase/firestore';
import { v4 as uuidv4 } from 'uuid';

export default class FormApi {
  static async list(): Promise<FormModel[]> {
    const db = firebase.firestore();

    const submittedFormQuery = await db.collection('forms').where('streamId', '>', 0).orderBy('streamId').get();
    const unSubmitFormQuery = await db.collection('forms').where('streamId', '==', 0).get();
    const submittedForms = submittedFormQuery.docs.map((doc) => doc.data() as FormModel);
    const unSubmitForms = unSubmitFormQuery.docs.map((doc) => doc.data() as FormModel);

    return submittedForms.concat(unSubmitForms);
  }

  static async listCeremonyAttendees(): Promise<AttendInfoModel[]> {
    const db = firebase.firestore();
    const docSnps = await db.collection('forms').where('isSubmitted', '==', true).orderBy('streamId').get();

    const allAttendees = [] as AttendInfoModel[];

    docSnps.docs.forEach((doc) => {
      const form = doc.data() as FormModel;
      const attendees = form.ceremonyForm.attendInfoList as AttendInfoModel[] | null;
      if (attendees) {
        attendees.forEach((attendee) => {
          attendee.timestamp = form.submitTime;
          allAttendees.push(attendee);
        });
      }
    });

    return allAttendees;
  }

  /**
   * @description save/update form document. if the form is anonymous, create formId for it.
   *
   * @param form the content of the anonymous form
   */
  static async save(form: FormModel): Promise<string> {
    const db = firebase.firestore();

    // random generate uuid for anonymous form
    if (form.formId === '') {
      form.formId = uuidv4();
    }

    if (!form.streamId) {
      form.streamId = await this.getStreamId();
    }

    const formContent = Utils.toObject(form);

    await db.collection('forms').doc(form.formId).set(formContent);

    return form.formId;
  }

  /**
   * @description delete a form, use it carefully.
   *
   * @param formId the id of a form, you can find it in FormModel.formId
   */
  static async delete(formId: string): Promise<void> {
    const db = firebase.firestore();
    return db.collection('forms').doc(formId).delete();
  }

  /**
   * @description reset states for all forms, use it carefully.
   *
   * reset states for all forms:
   * 1. shift submit state (isSubmitted) to last event submit state (isLastEventSubmitted)
   * 2. set submit state to not submit (isSubmitted = false)
   * 3. reset streamId (streamId = 0, where 0 is not a valid streamId)
   * 4. clear attend list of all forms (make ceremonyForm.attendInfoList empty list)
   *
   * reset form service state:
   * 1. reset streamCounter to 0 (where streamCounter is use to make streamId increment uniquely. see FormApi.getStreamId)
   */
  static async resetForms(): Promise<void> {
    const db = firebase.firestore();
    const batch = db.batch();

    const docSnps = await db.collection('forms').get();

    const emptyAttendInfo = Utils.toObject(new AttendInfo());

    // reset forms
    docSnps.docs.forEach((doc) => {
      console.log(`reset ${doc.id}`);

      const formRef = db.collection('forms').doc(doc.id);
      batch.update(formRef, {
        isLastEventSubmitted: (doc.data().isSubmitted === true),
        isSubmitted: false,
        streamId: 0,
        'ceremonyForm.attendInfoList': [emptyAttendInfo],
      });
    });

    // reset form variables
    const formVarRef = db.collection('variables').doc('form');
    batch.update(formVarRef, {
      streamCounter: 0,
    });

    await batch.commit();
  }

  /**
   * @description get a valid and unique increment id (start from 1).
   *
   * use transaction to ensure the stream is generated uniquely.
   *
   * @returns a valid and unique increment id.
   */
  static async getStreamId(): Promise<number> {
    const db = firebase.firestore();
    const formVarRef = db.collection('variables').doc('form');

    const streamId = await db.runTransaction(async (t) => {
      const doc = await t.get(formVarRef);
      const data = doc.data();
      if (!data) {
        throw new Error('variables.form not found');
      }
      const newStreamCounter = data.streamCounter + 1;
      await t.update(formVarRef, { streamCounter: newStreamCounter });
      return newStreamCounter;
    });
    return streamId;
  }
}
