import Vue from 'vue';
import Vuex from 'vuex';
import { AttendInfoModel, CeremonyFormModel } from '@/models/CeremonyFormModel';
import { EventInfoModel } from '@/models/EventFormModel';
import { Form, FormModel } from '@/models/FormModel';

Vue.use(Vuex);

const getDefaultState = () => ({
  homepageRoute: '/homepage/memberlist',

  membersList: [] as FormModel[],
  attendeesList: [] as AttendInfoModel[],
  confirmApplication: false, // 表示狀態用
  checkedRepeatList: false, // 表示狀態用
  form: new Form(), // memberForm
  signature: '', // 暫存區
  repeatFormList: [] as FormModel[],

  memberInfo: {} as CeremonyFormModel,

  eventFormStatus: 'viewOnly', // 分辨活動創建/檢視/編輯
  eventInfo: {} as EventInfoModel,
});

export default new Vuex.Store({
  state: getDefaultState(),
  mutations: {
    updateMemberList(state, array) {
      state.membersList = array;
    },
    updateAttendeesList(state, array) {
      state.attendeesList = array;
    },
    updateConfirmApplication(state, boolean) {
      state.confirmApplication = boolean;
    },
    updateCheckedRepeatList(state, boolean) {
      state.checkedRepeatList = boolean;
    },
    updateRepeatList(state, array) {
      state.repeatFormList = array;
    },
    updateMemberInfo(state, object) {
      state.memberInfo = object;
    },

    updateMemberForm(state, object) {
      state.form = object;
    },

    updateLongevity(state, array) {
      state.form.ceremonyForm.longevityBoards = array;
    },
    updatePrayFor(state, array) {
      state.form.ceremonyForm.prayBoards = array;
    },
    updateAttendInfo(state, array) {
      state.form.ceremonyForm.attendInfoList = array;
    },
    updateIsLastEventSubmitted(state, boolean) {
      state.form.isLastEventSubmitted = boolean;
    },
    updateIsSubmitted(state, boolean) {
      state.form.isSubmitted = boolean;
    },
    updateSubmitTime(state, submitTime) {
      state.form.submitTime = submitTime;
    },
    updateSignature(state, name) {
      state.form.signature = name;
    },
    saveSignature(state, name) {
      state.signature = name;
    },

    updateEventInfo(state, object) {
      state.eventInfo = object;
    },
    updateEventFormStatus(state, status) {
      state.eventFormStatus = status;
    },

    resetState(state) {
      Object.assign(state, getDefaultState());
    },
  },
  actions: {
  },
  modules: {
  },
});
